<!--
 * @Author: Marlon
 * @Date: 2020-02-18 10:29:16
 * @LastEditors: Marlon
 * @LastEditTime: 2020-02-21 08:15:13
 * @Description: 数据标准匹配
 -->
<template>
  <div class="dt-standard">
    <div class="area-query">
      <label for>项目：</label>
      <el-select v-model="proVal" filterable placeholder="请选择" size="small">
        <el-option
          v-for="item in proList"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
    </div>
    <div class="area-table">
      <TableDefault
        TId="1"
        :TTitle="tableTitle"
        :TData="tableData"
        :TIsShowPage="true"
        :TEdit="TEdit"
        @editBtnCb="editBtnCb"
      ></TableDefault>
    </div>
    <!-- 弹出框 -->
    <FormDialog
      :FDVisible="FDVisible"
      :FDTitle="FDTitle"
      FDWidth="500"
      :FDForm="FDForm"
      @editBtnCb="editBtnCbDiglog"
    ></FormDialog>
  </div>
</template>

<script>
import mixin from "mixin";
export default {
  mixins: [mixin],
  data() {
    return {
      //   项目名称
      proVal: "",
      //   项目列表
      proList: [
        {
          value: "",
          label: "所有项目"
        },
        {
          value: "选项2",
          label: "通用"
        },
        {
          value: "选项3",
          label: "南京"
        },
        {
          value: "选项4",
          label: "杭州"
        },
        {
          value: "选项5",
          label: "济南"
        }
      ],
      // 编辑-表格配置
      TEdit: {
        show: true,
        title: "操作",
        editTypes: [
          {
            label: "匹配数据标准"
          },
          {
            label: "查看匹配详情",
            link: "/"
          }
        ]
      },
      tableTitle: [
        {
          prop: "name",
          label: "所属项目"
        },
        {
          prop: "date",
          label: "共维护更新次数"
        },
        {
          prop: "address",
          label: "对应数据源(物理库)"
        },
        {
          prop: "address",
          label: "对应数据源(物理库)"
        }
      ],
      tableData: [
        {
          date: "122",
          name: "通用",
          address: "45"
        },
        {
          date: "122",
          name: "海淀",
          address: "45"
        },
        {
          date: "122",
          name: "山西",
          address: "45"
        },
        {
          date: "122",
          name: "济南",
          address: "45"
        },
        {
          date: "122",
          name: "山东",
          address: "45"
        },
        {
          date: "122",
          name: "贵州",
          address: "45"
        },
        {
          date: "122",
          name: "福州",
          address: "45"
        },
        {
          date: "122",
          name: "泰安",
          address: "45"
        },
        {
          date: "122",
          name: "南京",
          address: "45"
        },
        {
          date: "122",
          name: "广州",
          address: "45"
        }
      ],
      // 是否显示弹出框
      FDVisible: false,
      FDTitle: "",
      // 弹出框配置
      FDForm: []
    };
  },
  methods: {
    // 表格-回调
    editBtnCb(editItem, id) {
      console.log(editItem);
      let text = editItem.label;
      switch (text) {
        case "匹配数据标准":
          this.showDialog({ type: text });
          return;

          return;
        default:
          break;
      }
    },
    // 显示弹出框并修改配置
    showDialog({ type }) {
      switch (type) {
        case "匹配数据标准":
          this.FDTitle = "匹配数据标准";
          this.FDVisible = true;
          this.FDForm = [
            {
              type: "select",
              label: {
                name: "选择数据标准",
                width: "120"
              },
              width: "200",
              defaultModel: "",
              placeholder: "",
              option: [
                {
                  label: "海淀",
                  value: "haidian"
                }
              ]
            }
          ];
          return;
        default:
          return;
      }
    },
    // 弹出框-回调
    editBtnCbDiglog(obj, formReq) {
      this.FDVisible = false;
      if (obj === "确定") {
        // console.log(this.FDTitle);
        switch (this.FDTitle) {
          case "新增库":
            // TODO 发请求
            console.log(formReq);
            return;

          default:
            return;
        }
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.dt-standard {
  width: 100%;
  height: 100%;
  .area {
    &-query {
      float: right;
      margin: 0px 30px;
    }
    &-table {
      margin-top: 40px;
      width: 100%;
      height: calc(100% - 40px);
      overflow: auto;
    }
  }
}
</style>


